};
</script>



<style lang="scss" scoped>
@import "../../responsive/devices.scss";
@import "../../responsive/breakpoints.scss";
@import "../../scss/cssBase.scss";
@import "../../scss/project.scss";


#main{
  padding-top: 7rem;
}



.realisation__content{
  margin-bottom: 5rem;
}
.realisation__text{
  font-size: var(--small-font-size);
}



.fashion__containervideo{
  max-height: 400px !important;
  height: 100vh;
}



.realisation__img {
  width: 100%;
  height: 72vh;

  background-size: cover;
  margin: 0rem 0;
  background-repeat: no-repeat;
  background-attachment: inherit;
}





//FOR MEDIUM DEVICES taille moyene à partir de 768px
  @include md{
    .fashion__containervideo{
    max-height: 600px !important;
  }
 }






//FOR LARGES DEVICES taille grande / A partir de 1024px
  @include xl{
    .fashion__containervideo{
     max-height: 800px !important;
  }
 }






</style>


