<template>
  <div id="project">
    

    <article id="main_project">

      <!-- <div class="box-slider"> -->
        <!--- DEBUT CONTAINER ------------------------------------------------->
        <div class="slider">
          <button  @click="prev" type="button" class="button button__slider">
            <i class="ri-arrow-left-s-line icone icone-left"></i>
          </button>

          <div class="container-slides" :style="{transform: `translateX(${index}px)`, transition:`${transition}`}">
            <img src="../../assets/slider/1.jpeg" alt="" class="img-slider">
            <img src="../../assets/slider/2.jpeg" alt="" class="img-slider">
            <img src="../../assets/slider/3.jpeg" alt="" class="img-slider">
            <img src="../../assets/slider/4.jpeg" alt="" class="img-slider">
            <img src="../../assets/slider/5.jpeg" alt="" class="img-slider">
            <img src="../../assets/slider/6.jpeg" alt="" class="img-slider">
          </div>

          <button @click="next" type="button" class="button button__slider">
            <i class="ri-arrow-right-s-line icone icone-right"></i>
          </button> 
      </div>
      <!--- FIN CONTAINER -------------------------------------------------->

    <!-- </div> -->
  </article>



  </div>
</template>




<script>
export default {
 
  data:function(){
    return{
      index:0,
      transition: "transform 0.2s ease"
    }
  },
  methods:{
    next(){
      console.log(this.index);
      if(this.index === -1500){
        this.index = 0;
      } else {
        this.index -= 500;
      }
    },
    prev(){
      if(this.index === 0){
        this.index = -1500;
      } else {
        this.index += 500;
      }
    }
  }
}
</script>






<style  lang="scss" scoped>
@import "../../responsive/devices.scss";
@import "../../responsive/breakpoints.scss";
@import "../../scss/cssBase.scss";
@import "../../scss/project.scss";


// #app{
//   background-color: green;
//   justify-content: center;
//   align-items: center;
// }


#main_project{
  border: 1px solid red;
  width: 502px;

  display: block;
  margin: auto;
  padding-top: 2rem;
}



.box-slider{
  width: 500px;
}


//-----------DEBUT SLIDER------------------//
.slider{
  width: auto;
  height: auto;
  margin: 100px auto 0;
  // background: #000;
  overflow: hidden;
  position: relative;
}

.container-slides{
  display: flex;
}
.img-slider{
  width: 100%;
  height: auto;
}




.button__slider{
  outline: none;
  border: none;
  width: 50px;
  height: 50px;
  border-radius:50%;
  display: block;
  position: absolute;
  z-index: 1000;
  cursor: pointer;
  font-size: 50px;
  display: flex;
  justify-content:center;
  align-items:center;
  color:red ;
}

.icone{
  width: 50%;
}

.icone-left{
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.icone-right{
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
}


//-----------FIN SLIDER------------------//






</style>


