
import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router"

// import VideoBackground from 'vue-responsive-video-background-player'
import VideoBackground from './VideoBackground.vue'

Vue.component('video-background', VideoBackground)





Vue.use(VueRouter)
const router = new VueRouter({
	mode : "hash",
	routes:[

  
    {
      path:'/',
      components: require ('./components/pages/home.vue'),
      name:"home",

      meta : {
          requiresAuth:true,
          reload: true
      },
  },
  {
    path:'/1',
    components: require ('./components/pages/project_1.vue'),
    name:"project_1",
    meta : {
        requiresAuth:true
    }
},
{
  path:"*",
  redirect:"/"
}
  ],
  scrollBehavior: function (to) { //pour la navigation dans MENU avec "to" et "h"
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  }
  
  // ,
  //  scrollBehavior (to, from, savedPosition) { //ne fonction pas quand "to.hash" est activé
  //   return { x: 0, y: 0 }
  // }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
 
}).$mount('#app')



